@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root {
  --color-00ev: #121212;
  --color-01ev: #1e1e1e;
  --color-02ev: #232323;
  --color-03ev: #252525;
  --color-04ev: #272727;
  --color-06ev: #2c2c2c;
  --color-08ev: #2f2f2f;
  --color-12ev: #333333;
  --color-16ev: #353535;
  --color-24ev: #383838;

  --hover-00ev: #1b1b1b;
  --hover-01ev: #272727;
  --hover-02ev: #2c2c2c;
  --hover-03ev: #2e2e2e;
  --hover-04ev: #2f2f2f;
  --hover-06ev: #343434;
  --hover-08ev: #373737;
  --hover-12ev: #3b3b3b;
  --hover-16ev: #3d3d3d;
  --hover-24ev: #404040;

  --text-color-title: rgba(255, 255, 255, 0.87);
  --text-color-gray: rgba(255, 255, 255, 0.69);
  --text-color-minor: rgba(255, 255, 255, 0.38);

  --mat-tier1-color: #ffad33;
  --mat-tier1-color-dim: #ffbd5c;
  --mat-tier2-color: #c299ff;
  --mat-tier2-color-dim: #ceadff;
  --mat-tier3-color: #87acf2;
  --mat-tier3-color-dim: #9fbdf5;
  --mat-tier4-color: #b9f159;
  --mat-tier4-color-dim: #c7f47a;
  --mat-tier5-color: #a5a5a5;
  --mat-tier5-color-dim: #b7b7b7;

  --edge-tier1-color: #ff9900;
  --edge-tier1-color-dim: #ffad33;
  --edge-tier2-color: #b380ff;
  --edge-tier2-color-dim: #c299ff;
  --edge-tier3-color: #6997ef;
  --edge-tier3-color-dim: #87acf2;
  --edge-tier4-color: #a8ee30;
  --edge-tier4-color-dim: #b9f159;
  --edge-tier5-color: #8f8f8f;
  --edge-tier5-color-dim: #a5a5a5;

  --hero-tier1-color: #ff6f00;
  --hero-tier2-color: #ffae00;
  --hero-tier3-color: #b380ff;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100%;
  min-width: 100%;
  background-color: #121212;
  background-color: var(--color-00ev);
  color: white;
}

p, ul, h1 {
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  min-height: 100%;
  min-width: 100%;
}

::-webkit-scrollbar {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
  width: 12px;
  background-color: #333437;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
  background-color: #555;
}

::-webkit-scrollbar:horizontal {
  height: 12px;
}

.hero-card {
	background-color: var(--hover-01ev);
	margin: 5px;
	display: grid;
	grid-template-rows: 50px auto;
	grid-template-columns: auto 40px;
	grid-template-areas:
			"name cross"
			"card-content card-content";
}

.hero-card_name {
	grid-area: name;
	font-family: 'Roboto Condensed', sans-serif;
	color: var(--text-color-title);
	font-size: 30px;
	font-weight: 500;
	padding-left: 15px;
	padding-top: 5px;
	display: flex;
	align-items: center;
}

.hero-card_cross {
	grid-area: cross;
	display: flex;
	justify-content: flex-end;
	color: var(--text-color-title);
}

.hero-card_cross svg{
	cursor: pointer;
}

.hero-card_content {
	grid-area: card-content;
}

.hero-card_controls {
	display: grid;
	grid-template-rows: 160px 70px;
	grid-template-columns: 80px 160px 205px;
	grid-template-areas:
			"elite portrait upgrade"
			"skills skills skills";
}

.hero-card_portrait {
	display: flex;
	justify-content: center;
	align-items: center;
}

.hero-card_portrait img {
	grid-area: portrait;
	border: 5px solid rgba(255, 255, 255, 0.8);
}

.hero-card_elite {
	grid-area: elite;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
}

.hero-button_elite {
	width: 60px;
	height: 60px;
}

.hero-card_skills {
	grid-area: skills;
	display: flex;
	flex-flow: column;
	justify-content: space-around;
	padding-top: 10px;
}

.hero-card_common-skills {
	display: flex;
	justify-content: space-around;
}

.hero-button_skills {
	width: 50px;
	height: 50px;
}

.hero-card_specific-skills {
	grid-area: upgrade;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	font-family: 'Roboto Condensed', sans-serif;
	font-weight: 300;
	font-size: 20px;
	color: var(--text-color-title);
}

.hero-card_m {
	display: flex;
}

.hero-card_m p{
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50px;
	margin-right: 10px;
}

.hero-card_m-icons {
	display: flex;
	justify-content: center;
}

.hero-button_m-icons {
	width: 40px;
	height: 40px;
}

.hero-button {
	cursor: pointer;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	opacity: 60%;
	touch-action: manipulation;
	-webkit-tap-highlight-color: transparent;
}

.checked {
	opacity: 100%;
}

@media all and (max-width: 414px){
	.hero-card_controls {
		grid-template-rows: 160px auto 70px;
		grid-template-columns: 80px 294px;
		grid-template-areas:
			"elite portrait"
			"upgrade upgrade"
			"skills skills";
	}

	.hero-card {
		margin: 5px 0;
	}

	.hero-card_specific-skills {
		margin-top: 10px;
	}

	.hero-card_m {
		width: 100%;
		justify-content: space-around;
		margin-bottom: 10px;
	}

	.hero-card_m-icons {
		width: 55%;
		justify-content: space-between;
	}

	.hero-card_skills {
		margin-bottom: 10px;
	}
}

@media all and (max-width: 411px){
	.hero-card_controls {
		grid-template-columns: 80px 291px;
	}
}

@media all and (max-width: 375px){
	.hero-card_controls {
		grid-template-columns: 80px 255px;
	}
}

@media all and (max-width: 360px) {
	.hero-card_controls {
		grid-template-columns: 80px 240px;
	}
}
@media all and (max-width: 320px) {
	.hero-card_controls {
		grid-template-columns: 80px 200px;
	}
}

.hero-list {
	display: grid;
	grid-template-rows: 50px auto;
	grid-template-columns: auto 40px;
	grid-template-areas:
			"list-header list-close"
			"list-content list-content";
	width: 100%;
	margin: 5px;
	background-color: var(--hover-01ev);
}

.hero-list_content {
	display: flex;
	align-items: center;
	grid-area: list-content;
}

.hero-list_list {
	display: flex;;
	justify-content: center;
	flex-wrap: wrap;
}

.hero-list_header {
	grid-area: list-header;
	font-family: 'Roboto Condensed', sans-serif;
	color: var(--text-color-title);
	font-size: 30px;
	font-weight: 500;
	padding-left: 15px;
	padding-top: 5px;
	display: flex;
	align-items: center;
}

.hero-list_item {
	margin: 5px 5px;
	height: 100px;
	width: 75px;
	font-size: 14px;
	color: black;
	display: grid;
	grid-template-rows: 75px 25px;
	cursor: pointer;
}

.hero-list_item img {
	width: 75px;
	height: 75px;
}

.hero-list_name {
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(255, 255, 255, 0.6);
	font-family: 'Roboto', sans-serif;
}

.hero-list_close {
	grid-area: list-close;
	display: flex;
	justify-content: flex-end;
	cursor: pointer;
	color: var(--text-color-title);
}

.hero_6star {
	background-color: var(--hero-tier1-color);
}

.hero_5star {
	background-color: var(--hero-tier2-color);
}

.hero_4star {
	background-color: var(--hero-tier3-color);
}

.flamebringer_font {
	font-size: 12px;
}

@media all and (max-width: 414px){
	.hero-list {
		margin: 5px 0;
	}
}



