.table_cell {
	width: 100%;
	height: 50px;
	color: black;
	font-family: 'Roboto', sans-serif;
	font-size: 20px;
	font-weight: 600;
	display: flex;
	justify-content: center;
	align-items: center;
}
.table-column {
	min-width: 100px;
	display: flex;
	flex-flow: column;
}

.table-column:nth-child(1) {
	border-left: none;
}

.table-column_title {
	font-family: 'Roboto Condensed', sans-serif;
	height: 20px;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(255, 255, 255, 0.6);
	color: black;
}

@media all and (max-width: 414px) {
	.table_content > .table-column{
		min-width: 50px !important;
	}
}
.table_img {
	width: 50px;
	height: 50px;
}

.table_mat-cell {
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.header {
	background-color: var(--color-12ev);
	display: flex;
	align-items: center;
	padding-left: 30px;
}

nav a{
	color: var(--text-color-title);
	font-family: 'Roboto Condensed', sans-serif;
	font-size: 24px;
	text-decoration: none;
}
.footer {
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: 'Roboto', sans-serif;
}
* {
	box-sizing: border-box;
}

.app-container {
	display: grid;
	grid-template-rows: 60px 1fr 100px;
	grid-template-columns: 1fr;
	grid-template-areas:
			"header"
			"content"
			"footer";
}

.header {
	grid-area: header;
}

.content-container {
	grid-area: content;
	display: flex;
	justify-content: center;
}

.content-block {
	padding: 20px 20px;
	display: flex;
	align-items: center;
	flex-flow: column;
	background-color: var(--color-01ev);
}

.footer {
	grid-area: footer;
}

@media (max-width: 970px) {
	.content-block {
		max-width: 495px;
	}
}


